.calendar-item__right { float: left; width: 78%; *zoom: 1; padding: 0 100px 0 15px; }
.calendar-item__right:after { content: " "; display: table; clear: both; }
@media only screen and (max-width: 1200px) { .calendar-item__right { width: 70%; padding: 7px 70px 5px 15px; } }
@media only screen and (max-width: 800px) { .calendar-item__right { width: 100%; float: none; padding: 10px 50px 10px 15px; } }
.calendar-item__list, .calendar-item__country { opacity: 0.7; font-family: "ProximaNova Regular", Arial, sans-serif; }
.calendar-item__day, .calendar-item__city { opacity: 0.9; }
.calendar-item__list { font-size: 0; margin-bottom: 4px; }
.calendar-item__list li { font-size: 16px; display: inline-block; vertical-align: top; padding: 0 18px 0 0; position: relative; }
.calendar-item__list li:first-child:before { display: none; }
.calendar-item__list li:before { content: ""; position: absolute; left: -10px; top: 50%; margin-top: -1px; width: 2px; height: 2px; background: #fff; border-radius: 1px; }
.calendar-item__day { font-size: 18px; font-family: "GaramondPremr Medium", Arial, sans-serif; }
.calendar-item__city { font-size: 60px; text-transform: uppercase; font-family: "ProximaNova Bold", Arial, sans-serif; float: left; padding: 0 35px 0 0; }
@media only screen and (max-width: 1200px) { .calendar-item__city { font-size: 40px; padding: 0 20px 0 0; } }
@media only screen and (max-width: 800px) { .calendar-item__city { padding: 0 0 10px; float: none; font-size: 28px; } }
.calendar-item__country { font-size: 22px; padding: 5px 0; overflow: hidden; }
@media only screen and (max-width: 1200px) { .calendar-item__country { font-size: 18px; padding: 3px 0; } }
@media only screen and (max-width: 800px) { .calendar-item__country { overflow: visible; display: block; } }


